
//  Grid layout
.grid {
    display: grid;
    
    grid-gap: 30px;
    &-2 {
        @extend .grid;
        @include mq('lg'){
            grid-template-columns: repeat(2, 1fr);
        }
    }
    &-3 {
        @extend .grid;
        @include mq('lg') {
            grid-template-columns: repeat(3, 1fr);
            justify-content: stretch;
        }
    }
    &-4 {
        @extend .grid;
        @include mq('md') {
            grid-template-columns: repeat(2, 1fr);
        }

        @include mq('lg') {
            grid-template-columns: repeat(4, 1fr);
        }
    }
}

