//  Flexbox layout

.flex {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    @for $i from 1 to 10 {
        &-#{$i} {
            flex-grow: $i;
        }
    }

    @include mq('md') {
        flex-direction: row;
    }
}