.header {
    .header-menu {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .company-logo img {
            margin: auto 0;
            max-width: 100%;
        }

        .buttons {
            display: flex;
            align-items: center;

            float: right;
            & > *  {
                margin-top: 0;
                margin-left: 8px;
            }
            
        }
    }
}

